export function RaisedButton ({text}) {
    return <div className="mt-12 2xl:mt-16" data-aos="fade-right" data-aos-delay="800" data-aos-duration="1000">
        <a className="inline-block cursor-pointer border-2 border-amber-500 transition bg-amber-500 hover:text-black-56 px-12 py-4  text-white font-semibold text-sm rounded shadow-lg uppercase tracking-wider" href>{text}</a>
    </div>
}

export function OutlinedButton ({text}) {
    return <div className="mt-12 2xl:mt-16" data-aos="fade-up" data-aos-delay="1200" data-aos-duration="1000">
        <a className="inline-block cursor-pointer transition hover:text-black-56 hover:bg-amber-500 px-12 py-4 border-2 border-amber-500 text-amber-500 font-semibold text-sm rounded shadow-lg uppercase tracking-wider" href>{text}</a>
    </div>
}