import './AboutMePageStyle.css'

import { CustomTitle } from './CustomTitle'
import { OutlinedButton, RaisedButton } from './Buttons'

export function AboutMeSection () {
    return <div className="bg-black-56">
        <div className="py-32 px-12 grid lg:grid-cols-3 2xl:max-w-screen-2xl">
            {/* Image */}
            <div className="flex lg:col-span-1" data-aos="fade-right" data-aos-duration="1000">
                <div className="block about-me-img-size border-8 border-yellow-500"></div>
                <img className="absolute border-8 border-black-87 shadow-lg transform translate-x-10 -translate-y-10 about-me-img-size object-cover" 
                    src="/assets/me_01.jpeg" 
                    alt="me"/>
            </div>

            {/* Texte */}
            <div className="pt-12 lg:col-span-2 xl:pl-20 lg:pl-32" data-aos="fade-left" data-aos-duration="700" data-aos-delay="700">
                <CustomTitle text="About Me" />

                <p className="pt-8 text-lg lg:max-w-xl text-white font-sans">My name is Titosy, I am a student in the Licence of Business Administration, Software Engineering and Artificial Intelligence at the Institut Supérieur Polytéchnique de Madagascar (ISPM).  
My main interest in computer science mobile development, I have skills in the recent technologies including React Native and also Flutter from Google. Passionate about design and front-end development, I care about completing the opportunities that are given to me by building modern and efficient solutions. Rigorous and exigent, I make sure to respect the best practices and standards development. Being also curious and open-minded, I like to experiment with the latest trends through daily monitoring.
</p>
                
                <div className="flex items-center space-x-6">
                    <RaisedButton text="hire me"/>
                    <OutlinedButton text="download cv"/>
                </div>
            </div>
        </div>
    </div>
}