import { ServiceCard } from './ServiceCard'


export function ServicesSection () {
    return <div className="py-16 px-12">
        <div className="inline-block" data-aos="fade-right" data-aos-duration="1000">
            <h2 className="pb-5 text-white font-bold text-3xl lg:text-4xl">Services</h2>
            <div className="w-6/12 border-b-4 border-amber-500"></div>
        </div>
        <p className="pt-8 text-lg lg:max-w-xl text-white font-sans" data-aos="fade-right" data-aos-delay="700" data-aos-duration="1000">Here are some services I can offer you, for the moment I am in full learning but I offer you the best I can do.</p>
        <div className="grid pt-12 md:grid-cols-2 xl:grid-cols-3 2xl:grid-cols-4">
            <ServiceCard color="blue" title="UX Research" animationDelay="400" text="Application i build follow the trends and standards in terms of UX."/>
            <ServiceCard color="purple"title="Mobile development" animationDelay="800" text="performance, quality and efficiency"/>
            <ServiceCard color="green" title="App Design" animationDelay="1000" text="Visuals that attracts customers"/>
        </div>
    </div>
}